<template>
  <div class="upgrade-wrapper">
    <wistia-player
      :video-id="videoId"
      :options="options"
      v-on="$listeners"
      @pause="pause"
      @end="end"
    ></wistia-player>
    <a href="#" @click="upgradeClick" class="upgrade-modal">
      <div class="upgrade-text">
        Want to enjoy Chinesepod without ads?<br>
        Click <strong><u>here</u></strong> to upgrade to Premium!
      </div>
    </a>
  </div>
</template>

<script>
  import WistiaPlayer from '@/components/Player/WistiaPlayer'

  export default {
    name: 'ContentAds',
    components: { WistiaPlayer },
    props: {
      videoId: {
        type: String,
        required: true
      }
    },
    computed: {
      upgradeLink () {
        return this.$store.state.userInfo.upgrade.upgradeLink
      }
    },
    data() {
      return {
        options: {
          controlsVisibleOnLoad: false,
          playSuspendedOffScreen: false,
          volumeControl: false,
          playbackRateControl: false,
          silentAutoPlay: 'allow',
          settingsControl: false,
          qualityControl: false,
          playbar: false,
          playButton: false,
          smallPlayButton: false,
          fullscreenButton: false,
          volume: 1,
          muted: 0,
          plugin: {
            share: {
              channels: ''
            }
          }
        }
      }
    },
    methods: {
      pause(video) {
        video.play()
      },
      end() {
        this.$emit('end')
      },
      async upgradeClick () {
        await this.$store.dispatch('logEvent', {
          action: 'click_preroll_ad',
          'event_category': 'advertising',
          'event_label': `Clicked Preroll Ad ${this.videoId}`
        });
        window.location.href = this.upgradeLink
      }
    },
    mounted () {
      this.$emit('play')
    }
  }
</script>

<style scoped>
  .upgrade-wrapper {
    position: relative;
  }

  .upgrade-modal {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    align-items: flex-end;
  }

  .upgrade-text {
    padding: 30px;
    width: 100%;
    flex-basis: 100%;
    background: rgba(0, 0, 0, 0.4);
    text-align: center;
    color: #fff;
  }
</style>
